"use client";

import { useState } from "react";
import { Button } from "@/components/ui/button";
import { Card, CardContent } from "@/components/ui/card";
import { Download, Play, Pause, Loader2 } from "lucide-react";
import Image from "next/image";

interface TrackInfo {
  id: string;
  title: string;
  artist: string;
  thumbnail: string;
  duration: string;
  audioUrl?: string;
}

interface MusicCardProps {
  track: TrackInfo;
}

export function MusicCard({ track }: MusicCardProps) {
  const [isDownloading, setIsDownloading] = useState(false);
  const [downloadProgress, setDownloadProgress] = useState(0);
  const [isPlaying, setIsPlaying] = useState(false);

  const handleDownload = async () => {
    setIsDownloading(true);
    setDownloadProgress(0);

    // Simulate download progress
    const interval = setInterval(() => {
      setDownloadProgress((prev) => {
        if (prev >= 100) {
          clearInterval(interval);
          setIsDownloading(false);
          return 100;
        }
        return prev + 10;
      });
    }, 200);

    // In real implementation, this would call the download API
    setTimeout(() => {
      clearInterval(interval);
      setIsDownloading(false);
      setDownloadProgress(100);
      
      // Create download link
      const link = document.createElement('a');
      link.href = '#'; // Would be actual audio URL
      link.download = `${track.title}.mp3`;
      link.style.display = 'none';
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
    }, 2000);
  };

  const togglePreview = () => {
    setIsPlaying(!isPlaying);
  };

  return (
    <Card className="bg-gray-900 border-gray-800 overflow-hidden">
      <CardContent className="p-0">
        <div className="relative">
          <Image
            src={track.thumbnail}
            alt={track.title}
            width={400}
            height={400}
            className="w-full aspect-square object-cover"
            priority
          />
          <div className="absolute inset-0 bg-gradient-to-t from-black/60 to-transparent" />
          
          <Button
            variant="ghost"
            size="icon"
            className="absolute top-4 right-4 bg-black/50 hover:bg-black/70 text-white"
            onClick={togglePreview}
          >
            {isPlaying ? (
              <Pause className="h-4 w-4" />
            ) : (
              <Play className="h-4 w-4" />
            )}
          </Button>
        </div>

        <div className="p-4 space-y-3">
          <div>
            <h3 className="font-semibold text-white line-clamp-2">
              {track.title}
            </h3>
            <p className="text-sm text-gray-400">{track.artist}</p>
            <p className="text-xs text-gray-500 mt-1">{track.duration}</p>
          </div>

          {isDownloading && (
            <div className="space-y-2">
              <div className="w-full bg-gray-800 rounded-full h-2">
                <div
                  className="bg-white h-2 rounded-full transition-all duration-300"
                  style={{ width: `${downloadProgress}%` }}
                />
              </div>
              <p className="text-xs text-gray-400 text-center">
                Downloading... {downloadProgress}%
              </p>
            </div>
          )}

          <Button
            className="w-full bg-white text-black hover:bg-gray-200"
            onClick={handleDownload}
            disabled={isDownloading}
          >
            {isDownloading ? (
              <>
                <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                Downloading...
              </>
            ) : (
              <>
                <Download className="mr-2 h-4 w-4" />
                Download MP3
              </>
            )}
          </Button>
        </div>
      </CardContent>
    </Card>
  );
}
