"use client";

import { useState } from "react";

interface TrackInfo {
  id: string;
  title: string;
  artist: string;
  thumbnail: string;
  duration: string;
  audioUrl?: string;
}

interface UseYouTubeSearchReturn {
  searchVideo: (url: string) => Promise<void>;
  loading: boolean;
  error: string | null;
  trackInfo: TrackInfo | null;
}

export function useYouTubeSearch(): UseYouTubeSearchReturn {
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [trackInfo, setTrackInfo] = useState<TrackInfo | null>(null);

  const extractVideoId = (url: string): string | null => {
    const regex = /(?:youtube\.com\/(?:[^\/]+\/.+\/|(?:v|e(?:mbed)?)\/|.*[?&]v=)|youtu\.be\/)([^"&?\/\s]{11})/;
    const match = url.match(regex);
    return match ? match[1] : null;
  };

  const searchVideo = async (url: string) => {
    setLoading(true);
    setError(null);
    setTrackInfo(null);

    try {
      const videoId = extractVideoId(url);
      
      if (!videoId) {
        throw new Error("Invalid YouTube URL");
      }

      // Mock implementation - replace with actual YouTube API call
      // For demo purposes, using mock data
      const mockTrackInfo: TrackInfo = {
        id: videoId,
        title: "Sample Track Title",
        artist: "Sample Artist",
        thumbnail: `https://img.youtube.com/vi/${videoId}/maxresdefault.jpg`,
        duration: "3:45",
        audioUrl: undefined // Would be populated by actual API
      };

      // Simulate API delay
      await new Promise(resolve => setTimeout(resolve, 1500));
      
      setTrackInfo(mockTrackInfo);
    } catch (err) {
      setError(err instanceof Error ? err.message : "Failed to fetch video info");
    } finally {
      setLoading(false);
    }
  };

  return {
    searchVideo,
    loading,
    error,
    trackInfo,
  };
}
