"use client";

import { useState } from "react";
import { SearchForm } from "@/components/search-form";
import { MusicCard } from "@/components/music-card";
import { useYouTubeSearch } from "@/hooks/use-youtube-search";

interface TrackInfo {
  id: string;
  title: string;
  artist: string;
  thumbnail: string;
  duration: string;
  audioUrl?: string;
}

export default function HomePage() {
  const [searchUrl, setSearchUrl] = useState("");
  const { searchVideo, loading, error, trackInfo } = useYouTubeSearch();

  const handleSearch = async (url: string) => {
    setSearchUrl(url);
    await searchVideo(url);
  };

  return (
    <div className="container mx-auto px-4 py-8 max-w-md">
      <div className="text-center mb-8">
        <h1 className="text-3xl font-bold mb-2 bg-gradient-to-r from-white to-gray-400 bg-clip-text text-transparent">
          YouTube Music Downloader
        </h1>
        <p className="text-gray-400 text-sm">
          Paste a YouTube URL to download music with album cover
        </p>
      </div>

      <SearchForm onSearch={handleSearch} loading={loading} />

      {error && (
        <div className="mt-4 p-4 bg-red-900/20 border border-red-800 rounded-lg">
          <p className="text-red-400 text-sm">{error}</p>
        </div>
      )}

      {trackInfo && (
        <div className="mt-6">
          <MusicCard track={trackInfo} />
        </div>
      )}

      {!trackInfo && !loading && (
        <div className="mt-12 text-center">
          <div className="w-24 h-24 mx-auto mb-4 bg-gray-800 rounded-full flex items-center justify-center">
            <svg
              className="w-12 h-12 text-gray-600"
              fill="none"
              stroke="currentColor"
              viewBox="0 0 24 24"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth={2}
                d="M9 19V6l12-3v13M9 19c0 1.105-1.343 2-3 2s-3-.895-3-2 1.343-2 3-2 3 .895 3 2zm12-3c0 1.105-1.343 2-3 2s-3-.895-3-2 1.343-2 3-2 3 .895 3 2zM9 10l12-3"
              />
            </svg>
          </div>
          <p className="text-gray-500 text-sm">
            Enter a YouTube URL to get started
          </p>
        </div>
      )}
    </div>
  );
}
